body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-one: #b3b3b3e0;
  --color-two: #3D2E20;
  --color-three: rgb(153, 115, 189);
  --color-four: #D9CFC1;
  --color-five: rgb(219, 219, 219);
}

[class*="col-"] {
  width: 100%;
}

html, body, .main {
  height:100%;
}

body {
  background-color: #b3b3b3e0;
  background-color: var(--color-one);
}

@media only screen and (min-width: 768px) {
  .col-1 {width: 12.5%;}
  .col-2 {width: 25%;}
  .col-3 {width: 37.5%;}
  .col-4 {width: 50%;}
  .col-5 {width: 62.5%;}
  .col-6 {width: 75%;}
  .col-7 {width: 87.5%;}
  .col-8 {width: 100%;}
  .navButton {width: 100%;}
  .main {min-height: 75vh;}
  .desktopBorder {border-left: 5px solid #D9CFC1;border-left: 5px solid var(--color-four);}
}

* {
  box-sizing: border-box;
}

[class*="col-"] {
  float: left;
  padding: 15px;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

h1 {
  padding: 0px 10px;
  color: rgb(153, 115, 189);
  color: var(--color-three);
  font-size: 5vh;
  vertical-align: text-top;
  text-align: center;
}
h2 {
  padding: 5px;
  color: rgb(153, 115, 189);
  color: var(--color-three);
  font-size: 4vh;
}
h3 {
  padding: 0px 5px;
  color: #3D2E20;
  color: var(--color-two);
}

.tweet {
  padding: 15px;
}

.border {
  border: 5px solid #D9CFC1;
  border: 5px solid var(--color-four);
}

.bold {
  font-weight: bold;
}

.navButton {
  height: 10vh;
  font-size: 3vh;
  color: #3D2E20;
  color: var(--color-two);
  background-color: rgb(153, 115, 189);
  background-color: var(--color-three);
  border: 2px solid #3D2E20;
  border: 2px solid var(--color-two);
  margin: 1vh 0vh;
}

.navButton:hover {
  color: rgb(153, 115, 189);
  color: var(--color-three);
  background-color: #3D2E20;
  background-color: var(--color-two);
}

.tweetBorder {
  padding: 10px;
  border: 5px solid #3D2E20;
  border: 5px solid var(--color-two);
  border-radius: 5px 25px 25px;
  background-color: #D9CFC1;
  background-color: var(--color-four)
}

.tweetHeader {
  padding: 5px;
  border: 3px solid #3D2E20;
  border: 3px solid var(--color-two);
  border-radius: 5px 25px 10px 10px;
}

.tweetBody {
  padding: 3px;
}

.tweetFooter {
  padding: 3px;
}

.textBox {
  padding: 0px 15px;
  font-family: Helvetica, sans-serif;
}

.textBox.large {
  font-size: 6vh;
}

.textBox.medium {
  font-size: 3vh;
}

.colorOne {
  color: #b3b3b3e0;
  color: var(--color-one);
}

.colorTwo {
  color: #3D2E20;
  color: var(--color-two);
}

.colorThree {
  color: rgb(153, 115, 189);
  color: var(--color-three);
}

.colorFour {
  color: #D9CFC1;
  color: var(--color-four);
}

.colorFive {
  color: rgb(219, 219, 219);
  color: var(--color-five);
}


#splash {
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(/static/media/splash.3ba660b5.jpeg);
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(/static/media/splash.3ba660b5.jpeg);
  background-origin: border-box;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  min-height: 75vh;
}

.no-padding {
  padding: 0px;
}
